<template>
  <el-row type='flex' justify='center'>
    <el-form class="Form" ref='loginForm' :model='loginFormModel' :rules='loginFormRules'>
      <h1 class="Form-title"></h1>
      <p class="Form-description"></p>

      <div class="Form-fields">
        <label class="Control-label"></label>
        <el-form-item prop='username'>
          <el-input class="ControlInput" clearable placeholder='' v-model='loginFormModel.username' @keyup.enter.native='submitForm("loginForm")'></el-input>
        </el-form-item>

        <label class="Control-label"></label>
        <el-form-item prop='password'>
          <el-input class="ControlInput" clearable placeholder='' v-model='loginFormModel.password' type='password' @keyup.enter.native='submitForm("loginForm")'></el-input>
        </el-form-item>

        <el-form-item>
          <el-button id='login-button' type='success' icon='el-icon-check' @click='submitForm("loginForm")'></el-button>
          <el-button id='reset-button' type='danger' icon='el-icon-close' @click='resetForm("loginForm")'></el-button>
        </el-form-item>
      </div>

    </el-form>
  </el-row>
</template>

<script>
export default {
  // https://vuejs.org/v2/guide/components.html#data-Must-Be-a-Function
  data: function() {
    return {
      loginFormModel: {
        username: null,
        password: null
      },
      loginFormRules: {
        username: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        password: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ]
      }
    };
  },
  // http://element-cn.eleme.io/#/zh-CN/component/form#form-methods
  // { ... }return{ ... }return
  methods: {
    submitForm(formName) {
      // 
      this.$refs[formName].validate(valid => {
        // 
        if (valid) {
          // 
          this.$http
            .post("/login", this.loginFormModel)
            .then(res => {
              // 
              if (res.data) {
                var usertype = res.data.usertype;
                if (
                  usertype != "student" &&
                  usertype != "teacher" &&
                  usertype != "admin"
                ) {
                  this.$message({
                    type: "error",
                    message: "",
                    showClose: true
                  });
                  return;
                }

                var user = {
                  usertype: null,
                  username: null
                };
                user.usertype = res.data.usertype;
                user.username = this.loginFormModel.username;

                this.$store.dispatch("login", user).then(() => {
                  var msgOK = {
                    type: "success",
                    message: "",
                    duration: 1000,
                    center: true
                  };
                  if (usertype == "student") {
                    msgOK.message += " " + user.username;
                    this.$message(msgOK);
                    this.$router.replace("/student");
                  } else if (usertype == "teacher") {
                    msgOK.message += " " + user.username;
                    this.$message(msgOK);
                    this.$router.replace("/teacher");
                  } else if (usertype == "admin") {
                    msgOK.message += " " + user.username;
                    this.$message(msgOK);
                    this.$router.replace("/admin");
                  }
                });
              } else {
                this.$message({
                  type: "error",
                  message: "",
                  showClose: true,
                  center: true
                });
              }
            })
            .catch(err => {
              console.log(err);
              this.$message({
                type: "error",
                message: "",
                showClose: true,
                center: true
              });
            });
        } else {
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
};
</script>

<style lang="scss" type="text/scss">
@import "./Login.scss";
#login-button {
  width: 70%;
}

</style>
