<template>
  <el-container direction='vertical'>
    <el-button-group>
      <el-button type='primary' icon='el-icon-refresh' @click='getCourses()'></el-button>
      <el-button type='success' icon='el-icon-plus' @click='showAddCrsForm()'></el-button>
      <el-button type='danger' icon='el-icon-minus' @click='handleDeleteSels()'></el-button>
    </el-button-group>

    <el-dialog :title='crsFormTitle' :visible.sync='crsFormVisible'>
      <el-form ref='crsForm' :rules='crsFormRules' :model='crsFormModel' label-width="80px" status-icon>
        <el-form-item prop='cID' label=''>
          <el-input :disabled="cIDInputDisabled" v-model='crsFormModel.cID'>
            <template slot="prepend">C</template>
          </el-input>
        </el-form-item>
        <el-form-item prop='tID' label=''>
          <el-input v-model='crsFormModel.tID'>
              <template slot="prepend">T</template>
          </el-input>
        </el-form-item>
        <el-form-item prop='cName' label=''>
          <el-input v-model='crsFormModel.cName'></el-input>
        </el-form-item>
        <el-form-item prop='credit' label=''>
          <el-input v-model='crsFormModel.credit'></el-input>
        </el-form-item>
        <el-form-item prop='semester' label=''>
          <el-input v-model='crsFormModel.semester'></el-input>
        </el-form-item>
      </el-form>
      <div slot='footer'>
        <el-button v-if="rstButtonVisible" @click='resetCrsForm()'></el-button>
        <el-button @click='hideCrsForm()'></el-button>
        <el-button v-if='addButtonVisible' type='primary' @click='handleAdd()'></el-button>
        <el-button v-if='edtButtonVisible' type='primary' @click='handleEditFromForm()'></el-button>
      </div>
    </el-dialog>

    <el-table :data='crsTableData' stripe max-height='500' @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="40" />
      <el-table-column prop="cID" label="" width="70" />
      <el-table-column prop="tID" label="" width="70" />
      <el-table-column prop="cName" label="" width="150" />
      <el-table-column prop="credit" label="" width="50" />
      <el-table-column prop="semester" label="" />
      <el-table-column fixed="right" label="" width="200">
        <template slot-scope="scope">
          <el-button icon='el-icon-edit' size='mini' @click='handleEditFromMenu(scope.$index, scope.row)'></el-button>
          <el-button icon='el-icon-delete' size='mini' type='danger' @click='handleDeleteFromMenu(scope.$index, scope.row)'></el-button>
        </template>
      </el-table-column>
    </el-table>
  </el-container>
</template>

<script>
export default {
  data() {
    return {
      selectedCourses: [],
      crsTableData: [
        {
          cID: "",
          tID: "",
          cName: "",
          credit: "",
          semester: ""
        }
      ],
      crsFormRules: {
        cID: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        tID: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        cName: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        credit: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        semester: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ]
      },
      cIDInputDisabled: false,
      rstButtonVisible: false,
      addButtonVisible: false,
      edtButtonVisible: false,
      crsFormVisible: false,
      crsFormTitle: "",
      crsFormModel: {
        cID: "",
        tID: "",
        cName: "",
        credit: "",
        semester: ""
      }
    };
  },

  created: function() {
    this.getCourses();
  },

  methods: {
    resetCrsForm() {
      this.resetCrsFormModel();
      this.$refs.crsForm.resetFields();
    },

    hideCrsForm() {
      this.crsFormVisible = false;
    },

    showAddCrsForm() {
      this.crsFormTitle = "";
      this.cIDInputDisabled = false;
      this.rstButtonVisible = true;
      this.addButtonVisible = true;
      this.edtButtonVisible = false;
      this.crsFormVisible = true;
    },

    showEdtCrsForm() {
      this.crsFormTitle = "";
      this.cIDInputDisabled = true;
      this.rstButtonVisible = false;
      this.addButtonVisible = false;
      this.edtButtonVisible = true;
      this.crsFormVisible = true;
    },

    resetCrsFormModel() {
      this.crsFormModel = {
        cID: "",
        tID: "",
        cName: "",
        credit: "",
        semester: ""
      };
    },

    getCourses() {
      this.$http
        .get("/admin/course")
        .then(res => {
          this.crsTableData = res.data;
        })
        .catch(err => {
          this.$message({
            type: "error",
            message: "getCourses():",
            showClose: true,
            center: true
          });
        });
    },

    handleAdd() {
      this.$refs.crsForm.validate(valid => {
        if (valid) {
          var course = this.crsFormModel;
          course.cID = "C" + course.cID;
          course.tID = "T" + course.tID;
          this.addCourse(course);
        } else {
          return false;
        }
      });
    },

    addCourse(course) {
      this.hideCrsForm();
      this.$http
        .post("/admin/course", course)
        .then(res => {
          if (res.data.restype == "success") {
            this.$message({
              type: "success",
              message: "",
              showClose: true,
              center: true
            });
            this.getCourses();
            this.resetCrsFormModel();
          } else {
            this.$message({
              type: "error",
              message: "",
              showClose: true,
              center: true
            });
          }
        })
        .catch(err => {
          console.log(err);
          this.$message({
            type: "error",
            message: "addCourse():",
            showClose: true,
            center: true
          });
        });
    },

    handleEditFromMenu(index, row) {
      // model=row
      // 
      // 
      this.crsFormModel.cID = row.cID;
      this.crsFormModel.tID = row.tID;
      this.crsFormModel.cName = row.cName;
      this.crsFormModel.credit = row.credit;
      this.crsFormModel.semester = row.semester;

      this.cIDInputDisabled = true;
      this.showEdtCrsForm();
    },

    handleEditFromForm() {
      this.$refs.crsForm.validate(valid => {
        if (valid) {
          this.edtCourse(this.crsFormModel);
        } else {
          return false;
        }
      });
    },

    edtCourse(course) {
      this.hideCrsForm();
      this.$http
        .put("/admin/course/" + course.cID, course)
        .then(res => {
          if (res.data.restype == "success") {
            this.$message({
              type: "success",
              message: "",
              showClose: true,
              center: true
            });
            this.getCourses();
            this.resetCrsFormModel();
          } else {
            this.$message({
              type: "error",
              message: "",
              showClose: true,
              center: true
            });
          }
        })
        .catch(err => {
          console.log(err);
          this.$message({
            type: "error",
            message: "edtCourse():",
            showClose: true,
            center: true
          });
        });
    },

    handleSelectionChange(selections) {
      this.selectedCourses = selections;
    },

    handleDeleteSels() {
      this.selectedCourses.forEach(course => {
        this.delCourse(course.cID);
      });
    },

    handleDeleteFromMenu(index, row) {
      this.$confirm("" + row.cID + "", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.delCourse(row.cID);
      });
    },

    delCourse(cID) {
      this.$http
        .delete("/admin/course/" + cID)
        .then(res => {
          if (res.data.restype == "success") {
            this.$message({
              type: "success",
              message: "",
              showClose: true,
              center: true
            });
            this.getCourses();
          } else {
            this.$message({
              type: "error",
              message: "",
              showClose: true,
              center: true
            });
          }
        })
        .catch(err => {
          console.log(err);
          this.$message({
            type: "error",
            message: "delCourse():",
            showClose: true,
            center: true
          });
        });
    }
  }
};
</script>
