<template>
  <el-container direction='vertical'>
    <el-button-group>
      <el-button type='primary' icon='el-icon-refresh' @click='getStudents()'></el-button>
      <el-button type='success' icon='el-icon-plus' @click='showAddStuForm()'></el-button>
      <el-button type='danger' icon='el-icon-minus' @click='handleDeleteSels()'></el-button>
    </el-button-group>

    <el-dialog :title='stuFormTitle' :visible.sync='stuFormVisible'>
      <el-form ref='stuForm' :rules='stuFormRules' :model='stuFormModel' label-width="80px" status-icon>
        <el-form-item prop='sID' label='' >
          <el-input :disabled="sIDInputDisabled" v-model='stuFormModel.sID'>
            <template slot="prepend">S</template>
          </el-input>
        </el-form-item>
        <el-form-item prop='sName' label='' >
          <el-input v-model='stuFormModel.sName'></el-input>
        </el-form-item>
        <el-form-item prop='classno' label='' >
          <el-input v-model='stuFormModel.classno'></el-input>
        </el-form-item>
        <el-form-item prop='sex' label='' >
          <el-select v-model='stuFormModel.sex' placeholder=''>
            <el-option label='' value=''></el-option>
            <el-option label='' value=''></el-option>
          </el-select>
        </el-form-item>
        <el-form-item prop='password' label='' >
          <el-input v-model='stuFormModel.password' ></el-input>
        </el-form-item>
      </el-form>
      <div slot='footer'>
        <el-button v-if="rstButtonVisible" @click='resetStuForm()'></el-button>
        <el-button @click='hideStuForm()'></el-button>
        <el-button v-if='addButtonVisible' type='primary' @click='handleAdd()'></el-button>
        <el-button v-if='edtButtonVisible' type='primary' @click='handleEditFromForm()'></el-button>
      </div>
    </el-dialog>

    <el-table :data='stuTableData' stripe max-height='500' @selection-change="handleSelectionChange">
      <el-table-column type='selection' width='40' />
      <el-table-column sortable prop='sID' label='' width='90' />
      <el-table-column prop='sName' label='' width='70' />
      <el-table-column prop='classno' label='' width='150' />
      <el-table-column prop='sex' label='' width='50' />
      <el-table-column prop='password' label='' />
      <el-table-column fixed='right' label='' width='200'>
        <template slot-scope='scope'>
          <el-button icon='el-icon-edit' size='mini' @click='handleEditFromMenu(scope.$index, scope.row)'></el-button>
          <el-button icon='el-icon-delete' size='mini' type='danger' @click='handleDeleteFromMenu(scope.$index, scope.row)'></el-button>
        </template>
      </el-table-column>
    </el-table>

  </el-container>
</template>

<script>
export default {
  data() {
    return {
      selectedStudents: [],
      stuTableData: [
        {
          sID: "",
          sName: "",
          classno: "",
          sex: "",
          password: ""
        }
      ],
      stuFormRules: {
        sID: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        sName: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        classno: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        sex: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        password: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ]
      },
      sIDInputDisabled: false,
      rstButtonVisible: false,
      addButtonVisible: false,
      edtButtonVisible: false,
      stuFormVisible: false,
      stuFormTitle: "",
      stuFormModel: {
        sID: "",
        sName: "",
        classno: "",
        sex: "",
        password: ""
      }
    };
  },

  created: function() {
    this.getStudents();
  },

  methods: {
    resetStuForm() {
      this.resetStuFormModel();
      this.$refs.stuForm.resetFields();
    },

    hideStuForm() {
      this.stuFormVisible = false;
    },

    showAddStuForm() {
      this.stuFormTitle = "";
      this.sIDInputDisabled = false;
      this.rstButtonVisible = true;
      this.addButtonVisible = true;
      this.edtButtonVisible = false;
      this.stuFormVisible = true;
    },

    showEdtStuForm() {
      this.stuFormTitle = "";
      this.sIDInputDisabled = true;
      this.rstButtonVisible = false;
      this.addButtonVisible = false;
      this.edtButtonVisible = true;
      this.stuFormVisible = true;
    },

    resetStuFormModel() {
      this.stuFormModel = {
        sID: "",
        sName: "",
        classno: "",
        sex: "",
        password: ""
      };
    },

    getStudents() {
      this.$http
        .get("/admin/student")
        .then(res => {
          this.stuTableData = res.data;
        })
        .catch(err => {
          this.$message({
            type: "error",
            message: "getStudents():",
            showClose: true,
            center: true
          });
        });
    },

    handleAdd() {
      this.$refs.stuForm.validate(valid => {
        if (valid) {
          var student = this.stuFormModel;
          student.sID = "S" + student.sID;
          this.addStudent(student);
        } else {
          return false;
        }
      });
    },

    addStudent(student) {
      this.hideStuForm();
      this.$http
        .post("/admin/student", student)
        .then(res => {
          if (res.data.restype == "success") {
            this.$message({
              type: "success",
              message: "",
              showClose: true,
              center: true
            });
            this.getStudents();
            this.resetStuFormModel();
          } else {
            this.$message({
              type: "error",
              message: "",
              showClose: true,
              center: true
            });
          }
        })
        .catch(err => {
          console.log(err);
          this.$message({
            type: "error",
            message: "addStudent():",
            showClose: true,
            center: true
          });
        });
    },

    handleEditFromMenu(index, row) {
      // model=row
      // 
      // 
      this.stuFormModel.sID = row.sID;
      this.stuFormModel.sName = row.sName;
      this.stuFormModel.classno = row.classno;
      this.stuFormModel.sex = row.sex;
      this.stuFormModel.password = row.password;

      this.sIDInputDisabled = true;
      this.showEdtStuForm();
    },

    handleEditFromForm() {
      this.$refs.stuForm.validate(valid => {
        if (valid) {
          this.edtStudent(this.stuFormModel);
        } else {
          return false;
        }
      });
    },

    edtStudent(student) {
      this.hideStuForm();
      this.$http
        .put("/admin/student/" + student.sID, student)
        .then(res => {
          if (res.data.restype == "success") {
            this.$message({
              type: "success",
              message: "",
              showClose: true,
              center: true
            });
            this.getStudents();
            this.resetStuFormModel();
          } else {
            this.$message({
              type: "error",
              message: "",
              showClose: true,
              center: true
            });
          }
        })
        .catch(err => {
          console.log(err);
          this.$message({
            type: "error",
            message: "edtStudent():",
            showClose: true,
            center: true
          });
        });
    },

    handleSelectionChange(selections) {
      this.selectedStudents = selections;
    },

    handleDeleteSels() {
      this.selectedStudents.forEach(student => {
        this.delStudent(student.sID);
      });
    },

    handleDeleteFromMenu(index, row) {
      this.$confirm("" + row.sID + "", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.delStudent(row.sID);
      });
    },

    delStudent(sID) {
      this.$http
        .delete("/admin/student/" + sID)
        .then(res => {
          if (res.data.restype == "success") {
            this.$message({
              type: "success",
              message: "",
              showClose: true,
              center: true
            });
            this.getStudents();
          } else {
            this.$message({
              type: "error",
              message: "",
              showClose: true,
              center: true
            });
          }
        })
        .catch(err => {
          console.log(err);
          this.$message({
            type: "error",
            message: "delStudent():",
            showClose: true,
            center: true
          });
        });
    }
  }
};
</script>
