          // tID: "",
          // tName: "",
          // email: "",
          // sex: "",
          // password: ""
<template>
  <el-container direction='vertical'>
    <el-button-group>
      <el-button type='primary' icon='el-icon-refresh' @click='getTeachers()'></el-button>
      <el-button type='success' icon='el-icon-plus' @click='showAddTeaForm()'></el-button>
      <el-button type='danger' icon='el-icon-minus' @click='handleDeleteSels()'></el-button>
    </el-button-group>

    <el-dialog :title='teaFormTitle' :visible.sync='teaFormVisible'>
      <el-form ref='teaForm' :rules='teaFormRules' :model='teaFormModel' label-width="80px" status-icon>
        <el-form-item prop='tID' label='' >
          <el-input :disabled="tIDInputDisabled" v-model='teaFormModel.tID'>
            <template slot="prepend">T</template>
          </el-input>
        </el-form-item>
        <el-form-item prop='tName' label='' >
          <el-input v-model='teaFormModel.tName'></el-input>
        </el-form-item>
        <el-form-item prop='email' label='' >
          <el-input v-model='teaFormModel.email'></el-input>
        </el-form-item>
        <el-form-item prop='sex' label='' >
          <el-select v-model='teaFormModel.sex' placeholder=''>
            <el-option label='' value=''></el-option>
            <el-option label='' value=''></el-option>
          </el-select>
        </el-form-item>
        <el-form-item prop='password' label='' >
          <el-input v-model='teaFormModel.password' ></el-input>
        </el-form-item>
      </el-form>
      <div slot='footer'>
        <el-button v-if="rstButtonVisible" @click='resetTeaForm()'></el-button>
        <el-button @click='hideTeaForm()'></el-button>
        <el-button v-if='addButtonVisible' type='primary' @click='handleAdd()'></el-button>
        <el-button v-if='edtButtonVisible' type='primary' @click='handleEditFromForm()'></el-button>
      </div>
    </el-dialog>

    <el-table :data='teaTableData' stripe max-height='500' @selection-change="handleSelectionChange">
      <el-table-column type='selection' width='40' />
      <el-table-column sortable prop='tID' label='' width='90' />
      <el-table-column prop='tName' label='' width='70' />
      <el-table-column prop='email' label='' width='250' />
      <el-table-column prop='sex' label='' width='50' />
      <el-table-column prop='password' label='' />
      <el-table-column fixed='right' label='' width='200'>
        <template slot-scope='scope'>
          <el-button icon='el-icon-edit' size='mini' @click='handleEditFromMenu(scope.$index, scope.row)'></el-button>
          <el-button icon='el-icon-delete' size='mini' type='danger' @click='handleDeleteFromMenu(scope.$index, scope.row)'></el-button>
        </template>
      </el-table-column>
    </el-table>

  </el-container>
</template>

<script>
export default {
  data() {
    return {
      selectedTeachers: [],
      teaTableData: [
        {
          tID: "",
          tName: "",
          email: "",
          sex: "",
          password: ""
        }
      ],
      teaFormRules: {
        tID: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        tName: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        email: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        sex: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        password: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ]
      },
      tIDInputDisabled: false,
      rstButtonVisible: false,
      addButtonVisible: false,
      edtButtonVisible: false,
      teaFormVisible: false,
      teaFormTitle: "",
      teaFormModel: {
        tID: "",
        tName: "",
        email: "",
        sex: "",
        password: ""
      }
    };
  },

  created: function() {
    this.getTeachers();
  },

  methods: {
    resetTeaForm() {
      this.resetTeaFormModel();
      this.$refs.teaForm.resetFields();
    },

    hideTeaForm() {
      this.teaFormVisible = false;
    },

    showAddTeaForm() {
      this.teaFormTitle = "";
      this.tIDInputDisabled = false;
      this.rstButtonVisible = true;
      this.addButtonVisible = true;
      this.edtButtonVisible = false;
      this.teaFormVisible = true;
    },

    showEdtTeaForm() {
      this.teaFormTitle = "";
      this.tIDInputDisabled = true;
      this.rstButtonVisible = false;
      this.addButtonVisible = false;
      this.edtButtonVisible = true;
      this.teaFormVisible = true;
    },

    resetTeaFormModel() {
      this.teaFormModel = {
        tID: "",
        tName: "",
        email: "",
        sex: "",
        password: ""
      };
    },

    getTeachers() {
      this.$http
        .get("/admin/teacher")
        .then(res => {
          this.teaTableData = res.data;
        })
        .catch(err => {
          this.$message({
            type: "error",
            message: "getTeachers():",
            showClose: true,
            center: true
          });
        });
    },

    handleAdd() {
      this.$refs.teaForm.validate(valid => {
        if (valid) {
          var teacher = this.teaFormModel;
          teacher.tID = "T" + teacher.tID;
          this.addTeacher(teacher);
        } else {
          return false;
        }
      });
    },

    addTeacher(teacher) {
      this.hideTeaForm();
      this.$http
        .post("/admin/teacher", teacher)
        .then(res => {
          if (res.data.restype == "success") {
            this.$message({
              type: "success",
              message: "",
              showClose: true,
              center: true
            });
            this.getTeachers();
            this.resetTeaFormModel();
          } else {
            this.$message({
              type: "error",
              message: "",
              showClose: true,
              center: true
            });
          }
        })
        .catch(err => {
          console.log(err);
          this.$message({
            type: "error",
            message: "addTeacher():",
            showClose: true,
            center: true
          });
        });
    },

    handleEditFromMenu(index, row) {
      // model=row
      // 
      // 
      this.teaFormModel.tID = row.tID;
      this.teaFormModel.tName = row.tName;
      this.teaFormModel.email = row.email;
      this.teaFormModel.sex = row.sex;
      this.teaFormModel.password = row.password;

      this.tIDInputDisabled = true;
      this.showEdtTeaForm();
    },

    handleEditFromForm() {
      this.$refs.teaForm.validate(valid => {
        if (valid) {
          this.edtTeacher(this.teaFormModel);
        } else {
          return false;
        }
      });
    },

    edtTeacher(teacher) {
      this.hideTeaForm();
      this.$http
        .put("/admin/teacher/" + teacher.tID, teacher)
        .then(res => {
          if (res.data.restype == "success") {
            this.$message({
              type: "success",
              message: "",
              showClose: true,
              center: true
            });
            this.getTeachers();
            this.resetTeaFormModel();
          } else {
            this.$message({
              type: "error",
              message: "",
              showClose: true,
              center: true
            });
          }
        })
        .catch(err => {
          console.log(err);
          this.$message({
            type: "error",
            message: "edtTeacher():",
            showClose: true,
            center: true
          });
        });
    },

    handleSelectionChange(selections) {
      this.selectedTeachers = selections;
    },

    handleDeleteSels() {
      this.selectedTeachers.forEach(teacher => {
        this.delTeacher(teacher.tID);
      });
    },

    handleDeleteFromMenu(index, row) {
      this.$confirm("" + row.tID + "", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.delTeacher(row.tID);
      });
    },

    delTeacher(tID) {
      this.$http
        .delete("/admin/teacher/" + tID)
        .then(res => {
          if (res.data.restype == "success") {
            this.$message({
              type: "success",
              message: "",
              showClose: true,
              center: true
            });
            this.getTeachers();
          } else {
            this.$message({
              type: "error",
              message: "",
              showClose: true,
              center: true
            });
          }
        })
        .catch(err => {
          console.log(err);
          this.$message({
            type: "error",
            message: "delTeacher():",
            showClose: true,
            center: true
          });
        });
    }
  }
};
</script>
