<template>
  <el-row type='flex' width='100px'>

    <el-form ref='pwdForm' :model='pwdFormModel' label-width='80px'>
      <div class="FormPwdEditFields">
        <el-form-item label=''>
          <el-tag>{{ user.usertype }}</el-tag>
        </el-form-item>
        <el-form-item label=''>
          <el-tag>{{ user.username }}</el-tag>
        </el-form-item>
        <el-form-item label='' prop='pwdOld'>
          <el-input type='password' clearable placeholder='' v-model='pwdFormModel.pwdOld'></el-input>
        </el-form-item>
        <el-form-item label='' prop='pwdNew'>
          <el-input type='password' clearable placeholder='' v-model='pwdFormModel.pwdNew'></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type='primary' @click='handleEditPassword()'></el-button>
        </el-form-item>
      </div>
    </el-form>

  </el-row>
</template>

<script>
export default {
  data() {
    return {
      pwdFormModel: {
        pwdOld: null,
        pwdNew: null
      },
      pwdFormRules: {
        pwdOld: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        pwdNew: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ]
      }
    };
  },
  // https://cn.vuejs.org/v2/guide/computed.html
  // 
  //  message  reversedMessage 
  //
  //  A A  A  getter
  computed: {
    user() {
      var u = this.$store.state.user;
      if (u == undefined || u == null) {
        return {
          usertype: "",
          username: ""
        };
      } else {
        return u;
      }
    }
  },
  methods: {
    handleEditPassword() {
      this.$refs.pwdForm.validate(valid => {
        if (valid) {
          this.editPassword({
            username: this.$store.state.user.username,
            password_old: this.pwdFormModel.pwdOld,
            password_new: this.pwdFormModel.pwdNew
          });
        } else {
          return false;
        }
      });
    },

    editPassword(editPasswordMsg) {
      this.$http
        .put("/password", editPasswordMsg)
        .then(res => {
          if (res.data.restype == "success") {
            this.pwdFormModel = {
              pwdOld: null,
              pwdNew: null
            };
            this.$message({
              type: "success",
              message: "",
              showClose: true
            });
          } else {
            this.$message({
              type: "error",
              message: "",
              showClose: true
            });
          }
        })
        .catch(err => {
          this.$message({
            type: "error",
            message: "",
            showClose: true,
            center: true
          });
        });
    }
  }
};
</script>

<style>
.FormPwdEditFields {
  width: 300px;
  border-radius: 10px;
  border: 1px solid lightgray;
  background-color: white;
  display: flex;
  flex-direction: column;
  padding: 24px 15px;
}
</style>
