import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import actions from './actions'

Vue.use(Vuex)

// 
// https://vuex.vuejs.org/zh/guide/

//  Vuex  store
export default new Vuex.Store({
  /* store (state)Vuex  Vue  store  store  */
  state: {
    logined: false,
    user: {
      usertype: null,
      username: null
      // password: null
    }
  },
  // mutations
  // mutations
  /*  store  store  (commit) mutation */
  mutations,
  // actions
  // actionscommitmutation
  actions
})
